@import "../variables/variables.scss";

@media (min-width: 1261px) {
    .rickandmorty .container-r {
        max-width: 1440px;
    }
    .rickandmorty {
        .main {
            .main-flex {
                .characters-block {
                    .character-card {
                        .char-descr {
                            display: none;
                        }
                    }
                }
            }
        }
    }
}
@media (min-width: 1240px) and (max-width: 1460px) {
    .rickandmorty .container-r {
        max-width: 1230px;
    }
    .rickandmorty {
        .App-header {
            padding: 0px 20px;
        }
        .main {
            margin-top: 20px;
            padding: 0px 20px;
            .random-char {
                .char-block {
                    width: 50%;
                    .description-block {
                        font-size: 22px;
                        line-height: 140%;
                    }
                }
                .renew_block {
                    width: 45%;
                    .renew-text {
                        font-size: 28px;
                    }
                }
            }
            .main-flex {
                padding: 20px;
                justify-content: space-between;
                .characters-block {
                    width: 100%;
                    gap: 30px;
                    .character-card {
                        height: 390px;
                        max-width: 250px;
                        padding: 20px;
                        .character-img {
                            width: 210px;
                            height: 210px;
                        }
                        .char-descr {
                            display: none;
                        }

                        .linkto {
                            width: 120px;
                        }
                    }
                }
                .left-side {
                    width: 25%;
                    .left-side-wrap {
                        .single-char-info {
                            .single-char-block {
                                .single-char-img {
                                    align-self: center;
                                    width: 220px;
                                    height: 220px;
                                    border-radius: 5px;
                                }
                            }
                        }
                    }
                }
            }
        }
        .episodes {
            .episodes-list {
                padding: 0 20px;
            }
        }
        .single-page {
            height: 100%;
            padding: 0 20px;
        }
    }
}
@media (min-width: 993px) and (max-width: 1239px) {
    .rickandmorty .container-r {
        max-width: 960px;
    }
    .rickandmorty {
        .App-header {
            padding: 0px 20px;
        }
        .main {
            margin-top: 20px;
            padding: 0px 20px;
            .random-char {
                .char-block {
                    width: 60%;
                    .description-block {
                        font-size: 20px;
                        line-height: 140%;
                    }
                }
                .renew_block {
                    width: 38%;
                    .renew-text {
                        font-size: 22px;
                    }
                }
            }
            .main-flex {
                padding: 20px;
                justify-content: space-between;

                .wrap {
                    width: 65%;
                    display: flex;
                    flex-direction: column;
                    gap: 30px;
                    align-items: center;

                    .characters-block {
                        width: 100%;
                        justify-content: space-between;
                        flex-wrap: wrap;
                        .character-card {
                            height: 400px;
                            width: 270px;
                            padding: 15px;
                            .character-img {
                                width: 220px;
                                height: 220px;
                            }
                            .char-descr {
                                display: none;
                            }
                            .linkto {
                                width: 120px;
                            }
                        }
                    }
                }
                .left-side {
                    width: 30%;
                    .left-side-wrap {
                        .single-char-info {
                            font-size: 18px;
                            .single-char-block {
                                .single-char-img {
                                    align-self: center;
                                    width: 220px;
                                    height: 220px;
                                    border-radius: 5px;
                                }
                            }
                        }
                    }
                }
            }
        }
        .episodes {
            .episodes-list {
                .episode-single {
                    .episode-wrap {
                        gap: 15px;
                    }
                    .person-episode {
                        padding: 10px 20px 20px 20px;
                        justify-content: space-between;

                        .person-item {
                            width: 260px;
                            min-height: 340px;
                        }
                    }
                }
            }
        }
        .single-page {
            height: 100%;
            padding: 0 20px;
        }
    }
}
@media (min-width: 768px) and (max-width: 992px) {
    .rickandmorty .container-r {
        max-width: 750px;
    }
    .rickandmorty {
        .App-header {
            padding: 0px 20px;
        }
        .main {
            margin-top: 20px;
            padding: 0px 20px;
            .random-char {
                flex-wrap: wrap;
                gap: 20px;
                max-height: 100%;
                justify-content: center;
                .char-block {
                    width: 100%;
                    .description-block {
                        font-size: 26px;
                        line-height: 140%;
                    }
                }
                .renew_block {
                    width: 100%;
                    flex-direction: row;
                    .renew-text {
                        font-size: 22px;
                    }
                }
            }
            .main-flex {
                padding: 20px;
                justify-content: space-between;

                .wrap {
                    width: 55%;
                    display: flex;
                    flex-direction: column;
                    gap: 30px;
                    align-items: center;

                    .characters-block {
                        width: 100%;
                        justify-content: space-between;
                        flex-wrap: wrap;
                        .character-card {
                            height: 450px;
                            max-width: 350px;
                            padding: 15px;
                            .character-img {
                                width: 300px;
                                height: 300px;
                            }
                            .char-descr {
                                display: none;
                            }
                            .linkto {
                                width: 120px;
                            }
                        }
                    }
                }
                .left-side {
                    width: 40%;

                    .left-side-wrap {
                        .single-char-info {
                            font-size: 18px;
                            .single-char-block {
                                .single-char-img {
                                    align-self: center;
                                    width: 220px;
                                    height: 220px;
                                    border-radius: 5px;
                                }
                            }
                        }
                    }
                }
            }
        }
        .episodes {
            .episodes-list {
                padding: 0 20px;
                .episode-single {
                    font-display: column;
                    flex-wrap: wrap;

                    align-items: center;
                    .episode-wrap {
                        flex-direction: column;
                        flex-wrap: wrap;
                        gap: 20px;
                        width: 100%;
                        align-items: center;
                        .button.episode-heroes {
                            margin: 0 auto;
                        }
                    }
                    .person-episode {
                        justify-content: center;
                        width: 100%;
                        .lin {
                            display: flex;
                            justify-content: center;
                            .person-item {
                                width: 300px;
                                min-height: 470px;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                flex-wrap: wrap;
                                .person-image {
                                    width: 90%;
                                    height: auto;
                                }
                            }
                        }
                    }
                }
            }
        }
        .single-page {
            height: 100%;
            padding: 0 20px;
            .single-page-wrap {
                .single-img {
                    width: 320px;
                    height: 320px;
                }
            }
        }
    }
}
